<template>
	<view>
		<div class="question-con">
			<div class="question-con-inner">
				<div class="question-con-bigTitle">{{bigTitle}}</div>
				<div class="question-con-subject">
					<div class="question-con-smallTitle">
						{{id}}<span>.{{questionList.title}}</span>
					</div>
					<ul>
						<li @click="questionBtn(item.id)" v-for="item in questionList.value" :key="item.id"
							:class="[{error_bg:item.id == selId && item.id != questionList.answer},{right_bg:item.id == selId && item.id == questionList.answer}]">
							{{item.label}}
							<van-icon name="success" v-if="item.id == selId && item.id == questionList.answer" />
							<van-icon name="cross" v-if="item.id == selId && item.id != questionList.answer" />
						</li>
					</ul>
				</div>
				<div class="question-con-analysis" v-if="true">
					<ul>
						<li>您的答案：<span :class="error_red? 'error_red': ''">B</span></li>
						<li>正确答案：A</li>
						<li>解析:世界上最高的山峰是珠穆朗玛峰</li>
					</ul>
				</div>
			</div>
		</div>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				bigTitle: '',
				questionList: {
					title: '世界上最高峰是',
					answer: '1',
					value: [{
						id: 1,
						label: 'A.珠穆拉玛峰'
					}, {
						id: 2,
						label: 'B.梧桐山'
					}, {
						id: 3,
						label: 'C.莲花山'
					}, {
						id: 4,
						label: 'D.乔戈里峰'
					}]
				},
				selId: 0,
				error_red: true,
				showDialog: false,
			}
		},
		onLoad(option) {
			this.id = option.id
		},
		mounted() {

		},
		methods: {

		}
	}
</script>

